"use client"
import { useEffect, useState } from 'react';
import { onAuthStateChanged, User } from 'firebase/auth';
import { auth, db } from '@/config/firebase-config';
import { doc, getDoc } from 'firebase/firestore';
import { useParams, useRouter } from 'next/navigation';
import FollowButton from '../../../../components/ProfileComponents/FollowButton';
import FollowersSection from '../../../../components/ProfileComponents/FollowersSection';

interface UserProfile {
  username: string;
  email: string;
  createdAt: any;
}

export default function Profile() {
  const [user, setUser] = useState<User | null>(null);
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [copied, setCopied] = useState(false);
  const [followingCount, setFollowingCount] = useState(0);
  const [followersCount, setFollowersCount] = useState(0);
  const router = useRouter();
  const params = useParams<{userID: string}>()

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user: User | null) => {
      setUser(user);

      try {
        // Fetch the profile user's data
        const userDoc = await getDoc(doc(db, "users", params.userID));
        if (userDoc.exists()) {
          setProfile(userDoc.data() as UserProfile);
        }

        // Fetch following count
        const followingRef = doc(db, "users", params.userID, "profile", "following");
        const followingDoc = await getDoc(followingRef);
        if (followingDoc.exists()) {
          const followingData = followingDoc.data();
          const userIds = followingData.userIds || [];
          setFollowingCount(userIds.length);
        } else {
          setFollowingCount(0);
        }

        // Fetch followers count
        const followersRef = doc(db, "users", params.userID, "profile", "followers");
        const followersDoc = await getDoc(followersRef);
        if (followersDoc.exists()) {
          const followersData = followersDoc.data();
          const userIds = followersData.userIds || [];
          setFollowersCount(userIds.length);
        } else {
          setFollowersCount(0);
        }
      } catch (error) {
        console.error("Error fetching profile:", error);
      } finally {
        setLoading(false);
      }
    });
    return () => unsubscribe();
  }, [router, params.userID]);

  const handleCopyLink = async () => {
    try {
      await navigator.clipboard.writeText(`${window.location.origin}/library/${params.userID}`);
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    } catch (error) {
      console.error("Failed to copy link:", error);
    }
  };

  // Check if viewing own profile
  const isOwnProfile = user?.uid === params.userID;

  if (loading) {
    return <div className="p-4">Loading...</div>;
  }

  if (!user || !profile) {
    return <div className="p-4">Profile not found</div>;
  }

  return (
    <div className="p-8 max-w-7xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Profile</h1>
      
      <div className="flex gap-6">
        {/* Left Column - Account and Library Settings */}
        <div className="flex-1 space-y-6">
          <div className="bg-[#141823] shadow rounded-lg p-6 border border-gray-500">
            <div className="flex items-center space-x-4 mb-4">
              {/* Profile Picture Placeholder */}
              <div className="w-16 h-16 bg-gray-600 rounded-full flex items-center justify-center flex-shrink-0">
                <div 
                  className="w-8 h-8 bg-gray-400"
                  style={{
                    maskImage: 'url(/user.svg)',
                    maskSize: 'contain',
                    maskRepeat: 'no-repeat',
                    maskPosition: 'center',
                    WebkitMaskImage: 'url(/user.svg)',
                    WebkitMaskSize: 'contain',
                    WebkitMaskRepeat: 'no-repeat',
                    WebkitMaskPosition: 'center'
                  }}
                />
              </div>
              
              <div className="flex flex-1 min-w-0 space-x-4 ">
                {/* Username and Member Since */}
                <div className="flex-shrink-0">
                  <div className="flex items-center space-x-3 mb-1">
                    <h2 className="text-2xl font-bold text-white">{profile.username}</h2>
                    <FollowButton 
                      currentUser={user}
                      targetUserId={params.userID}
                      isOwnProfile={isOwnProfile}
                    />
                  </div>
                  <p className="text-sm text-gray-400">
                    Member since {profile.createdAt?.toDate?.()?.toLocaleDateString() || 'Unknown'}
                  </p>
                </div>
                
                {/* Bio Section */}
                <div className="flex-1 min-w-0">
                  <div className="text-sm text-gray-300">
                    <p className="italic text-gray-400">No bio yet. Tell the community about yourself!</p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-[#141823] shadow rounded-lg p-6 border border-gray-500">
            <h2 className="text-xl font-semibold mb-4">Library Settings</h2>
            <div className="space-y-4">
              <div className="pt-2">
                <h3 className="text-sm font-medium text-gray-400 mb-2">Share Link</h3>
                <div className="flex items-center space-x-2">
                  <input 
                    type="text" 
                    value={`${window.location.origin}/library/${params.userID}`}
                    readOnly
                    className="flex-1 px-3 py-2 border border-gray-500 rounded-md text-sm bg-[#181e2c]"
                  />
                  <button 
                    onClick={handleCopyLink}
                    className={`w-20 px-4 py-2 rounded text-sm transition-colors ${
                      copied 
                        ? 'bg-green-500 text-white' 
                        : 'bg-[#616dc9] text-white hover:bg-[#5159b3]'
                    }`}
                  >
                    {copied ? 'Copied!' : 'Copy'}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Right Column - Following and Followers */}
        <FollowersSection 
          followingCount={followingCount}
          followersCount={followersCount}
          isOwnProfile={isOwnProfile}
        />
      </div>
    </div>
  );
}
