"use client"
import { useState } from 'react';
import FollowingModal from './FollowingModal';
import FollowersModal from './FollowersModal';

interface FollowersSectionProps {
  followingCount: number;
  followersCount: number;
  isOwnProfile: boolean;
  userId: string;
}

export default function FollowersSection({ 
  followingCount, 
  followersCount, 
  isOwnProfile,
  userId 
}: FollowersSectionProps) {
  const [showFollowingModal, setShowFollowingModal] = useState(false);
  const [showFollowersModal, setShowFollowersModal] = useState(false);
  return (
    <div className="w-80">
      <div className="bg-[#141823] shadow rounded-lg p-6 border border-gray-500">
        <div className="space-y-6">
          {/* Following Section */}
          <div>
            <h2 className="text-xl font-semibold mb-3">Following ({followingCount})</h2>
            <div className="space-y-2 min-h-[100px]">
              <div className="text-sm text-gray-500">
                {followingCount === 0 
                  ? (isOwnProfile ? "You're not following anyone yet." : "Not following anyone yet.") 
                  : `Following ${followingCount} user${followingCount !== 1 ? 's' : ''}`
                }
              </div>
              {/* Space for following accounts will go here */}
            </div>
          </div>

          {/* Followers Section */}
          <div>
            <h2 className="text-xl font-semibold mb-3">Followers ({followersCount})</h2>
            <div className="space-y-2 min-h-[100px]">
              <div className="text-sm text-gray-500">
                {followersCount === 0 
                  ? (isOwnProfile ? "No followers yet." : "No followers.") 
                  : `${followersCount} follower${followersCount !== 1 ? 's' : ''}`
                }
              </div>
              {/* Space for follower accounts will go here */}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
